	module.exports = {"data":{"markdownRemark":{"html":"<p>浦東省Gaoqiaoの人事局の研究室の特別研究員の会議に。一年に一度、今年は主に来年の研究作業の方法について話し合っています。</p>\n<p>研究室長は、理論的研究センター、政策コンサルティングセンター、人材技術センターに行政・人事研究所を設置するという基本的な考え方について話した。主なテーマは、世紀のリーダーシップ人材の育成と研究、国家公務員制度の問題と対応策の研究、現代の企業人事制度の研究、新世紀の上海における人材の質と開発研究、人材市場の舞台における矛盾と対策人材育成に関する研究と比較研究。これらのトピックは非常に貴重なトピックであり、中国社会の発展のための実践的かつ理論的な意義があります。</p>\n<p>近年の研究では、人々は社会経済の発展の後に避けられないアイデアである「人を中心とした経済発展」という概念を一般的に受け入れている。経済がそれほど発展していない場合、そのような考えを受け入れることは困難です。マズローの心理的ニーズのレベルにある要因があるかもしれません。人間開発に注意を払うことは、マルクス主義の重要な理想的かつ価値観である。中国の発展はまた、この理想的な方向に進む必要があります。マルクスが言ったことは、抽象的な理想ではなく、必然的な社会開発の論理を含む、開発された商品経済の検討の結果である。</p>\n<p>行政管理の観点からは、社会の深刻な変化や市場経済システムの発展に合わせて、管理理論を学ぶべきである。</p>\n<p>国と社会</p>\n<p>政府とビジネス</p>\n<p>政府と市場</p>\n<p>人々と市場</p>\n<p>これらは、おそらく社会発展の基本的な理論の有機的要素であろう。社会の発展は、物質の原動力、人間の中の力、そして制度の力から始まります。新しいシステムを確立するためには、これらの基本的な問題を解決する必要があります。これはすべての問題ではありませんが、少なくともそれが主要な問題です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0194.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0194.md","frontmatter":{"title":"11月25日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0194.md absPath of file >>> MarkdownRemark"}}

